import React, { useCallback, useContext, useMemo, useRef } from "react";
import { StyleSheet, View } from "react-native";
import { Card, CheckBox } from "react-native-elements";
import { PlayerContext } from "../backend/PlayerContext";
import {
  allocateResearchersToSchool,
  calculateResearcherCap,
  getBaseResearchRate,
  getFreeResearchers,
} from "../backend/research/Researchers";
import {
  getSchoolLevel,
  getUnlockedSchools,
  School,
} from "../backend/spells/ElementsAndSchools";
import { formatNumber } from "../utils/FormattingUtils";
import ActionsContainer from "./utility/ActionsContainer";
import { Card as UtilityCard } from "./utility/Card";
import { PlusMinusAllocator } from "./utility/PlusMinusAllocator";
import { Text } from "./utility/Text";
import { UIContext, useUIStateFromContext } from "./utility/UIContext";

function ResearchRow(props: { school: School; applyAll: boolean }) {
  const school = props.school;
  const playerContext = useContext(PlayerContext);
  const playerContextRef = useRef(playerContext);
  playerContextRef.current = playerContext;
  const freeResearchers = getFreeResearchers(playerContext);
  const hasFreeResearchers = freeResearchers > 0;

  const current =
    playerContext.research.researcherAllocation.schools?.[school] || 0;
  const onPressMinus = useCallback(
    () =>
      playerContextRef.current.apply(
        allocateResearchersToSchool(school, props.applyAll ? -Infinity : -1),
      ),
    [school, props.applyAll],
  );
  const onPressPlus = useCallback(
    () =>
      playerContextRef.current.apply(
        allocateResearchersToSchool(school, props.applyAll ? Infinity : 1),
      ),
    [school, props.applyAll],
  );
  const schoolLevel = getSchoolLevel(playerContext, school);

  const schoolText = useMemo(
    () => (
      <Text numberOfLines={3}>
        {school}
        {playerContext.primarySchool == school ? " (Primary)" : ""}:{" "}
        {"Lv" + schoolLevel}
      </Text>
    ),
    [school, playerContext.primarySchool, schoolLevel],
  );
  return useMemo(
    () => (
      <View
        key={school}
        style={[styles.row, styles.singleSchoolElementContainer]}
      >
        <View style={styles.schoolTextContainer}>{schoolText}</View>
        <View style={[styles.row, styles.rightHandSide]}>
          <Text>{current}</Text>
          <View style={styles.plusMinusAllocatorContainer}>
            <PlusMinusAllocator
              disableMinus={current === 0}
              disablePlus={!hasFreeResearchers}
              onPressMinus={onPressMinus}
              onPressPlus={onPressPlus}
            />
          </View>
        </View>
      </View>
    ),
    [schoolText, current, hasFreeResearchers, onPressMinus, onPressPlus],
  );
}

export function Research() {
  const playerContext = useContext(PlayerContext);
  const uiContext = useContext(UIContext);
  const playerContextRef = useRef(playerContext);
  playerContextRef.current = playerContext;
  const freeResearchers = getFreeResearchers(playerContext);

  const [applyAll, setApplyAll] = useUIStateFromContext(
    uiContext,
    "research_applyall",
    false,
  );

  const toggleApplyAll = useCallback(() => {
    setApplyAll(!applyAll);
  }, [applyAll, setApplyAll]);

  const bottomSection = useMemo(
    () => (
      <View>
        <View style={styles.assignAllContainer}>
          <CheckBox
            title="Assign / unassign all at once"
            checked={applyAll}
            textStyle={styles.checkboxText}
            containerStyle={styles.checkboxContainer}
            onPress={toggleApplyAll}
          />
        </View>
        <View style={[styles.row, styles.researcherCapacityContainer]}>
          <Text>Researchers available: </Text>
          <Text>
            {formatNumber(freeResearchers)}/
            {formatNumber(playerContext.research.totalResearchers)}
          </Text>
        </View>
        <View style={[styles.row, styles.researcherCapacityContainer]}>
          <Text>Researcher capacity: </Text>
          <Text>
            {formatNumber(playerContext.research.totalResearchers)}/
            {formatNumber(calculateResearcherCap(playerContext))}
          </Text>
        </View>
        <View style={[styles.row, styles.researcherCapacityContainer]}>
          <Text>Exp per researcher: </Text>
          <Text>
            {formatNumber(getBaseResearchRate(playerContext), {
              showDecimals: true,
            })}{" "}
            / sec
          </Text>
        </View>
        <View>
          <ActionsContainer area="Research" />
        </View>
      </View>
    ),
    [
      calculateResearcherCap(playerContext),
      getBaseResearchRate(playerContext),
      playerContext.research.totalResearchers,
      freeResearchers,
      applyAll,
      toggleApplyAll,
    ],
  );
  const schools = getUnlockedSchools(playerContext);

  const disableAllMinus =
    freeResearchers === playerContext.research.totalResearchers;
  const disableAllPlus = freeResearchers <= 0;
  const onPressMinusAll = useCallback(() => {
    playerContextRef.current.apply((state) => {
      for (let school in playerContext.research.researcherAllocation.schools) {
        allocateResearchersToSchool(
          school as School,
          applyAll ? -Infinity : -1,
        )(state);
      }
      return state;
    });
  }, [applyAll]);
  const onPressPlusAll = useCallback(() => {
    playerContextRef.current.apply((state) => {
      do {
        for (let school of getUnlockedSchools(state)) {
          allocateResearchersToSchool(school as School, 1)(state);
        }
      } while (applyAll && getFreeResearchers(state) > 0);
      return state;
    });
  }, [applyAll]);

  return useMemo(() => {
    return (
      <View>
        <UtilityCard>
          <Card.Title>Schools</Card.Title>
          {schools.map((school) => {
            return (
              <ResearchRow key={school} school={school} applyAll={applyAll} />
            );
          })}
          <View
            key="All"
            style={[styles.row, styles.singleSchoolElementContainer]}
          >
            <View style={styles.schoolTextContainer}>
              <Text numberOfLines={3}>Assign to every School</Text>
            </View>
            <View style={[styles.row, styles.rightHandSide]}>
              <View style={styles.plusMinusAllocatorContainer}>
                <PlusMinusAllocator
                  disableMinus={disableAllMinus}
                  disablePlus={disableAllPlus}
                  onPressMinus={onPressMinusAll}
                  onPressPlus={onPressPlusAll}
                />
              </View>
            </View>
          </View>
        </UtilityCard>
        {bottomSection}
      </View>
    );
  }, [
    schools.length,
    bottomSection,
    applyAll,
    disableAllMinus,
    disableAllPlus,
    onPressMinusAll,
    onPressPlusAll,
  ]);
}

const styles = StyleSheet.create({
  row: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "center",
  },
  plusMinusAllocatorContainer: {
    marginLeft: 16,
  },
  assignAllContainer: {
    marginTop: 8,
    paddingHorizontal: 16,
  },
  researcherCapacityContainer: {
    marginTop: 4,
    paddingHorizontal: 16,
  },
  singleSchoolElementContainer: {
    marginVertical: 4,
  },
  checkboxContainer: {
    marginLeft: 0,
    marginTop: 8,
    padding: 0,
  },
  checkboxText: {
    fontWeight: "normal",
  },
  rightHandSide: {
    marginLeft: 16,
  },
  schoolTextContainer: {
    flex: 1,
  },
});
