import { PlayerContextState } from "./PlayerContext";

const cache: Record<string, [number, number]> = {};

export function cacheByTickCount(
  state: PlayerContextState,
  keyFn: (state: PlayerContextState) => string,
  valueFn: (state: PlayerContextState) => any,
) {
  if (state.isFake) {
    return valueFn(state);
  }
  const tickCount = state.global.tickCount;
  const key = keyFn(state);
  if (!cache?.[key] || cache[key][0] != tickCount) {
    cache[key] = [tickCount, valueFn(state)];
  }
  return cache[key][1];
}
